import Divider from '@mui/material/Divider'
import MenuList from '@mui/material/MenuList'
import MenuItem from '@mui/material/MenuItem'
import ListItemText from '@mui/material/ListItemText'
import MuiMenu from '@mui/material/Menu'

import ListItemIcon from '@mui/material/ListItemIcon'
import RadioButtonCheckedOutlinedIcon from '@mui/icons-material/RadioButtonCheckedOutlined'
import BorderColorOutlinedIcon from '@mui/icons-material/BorderColorOutlined'
import DashboardTwoToneIcon from '@mui/icons-material/DashboardTwoTone'
import FullscreenTwoToneIcon from '@mui/icons-material/FullscreenTwoTone'
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome'
import ClosedCaptionOutlinedIcon from '@mui/icons-material/ClosedCaptionOutlined'
import PhoneForwardedIcon from '@mui/icons-material/PhoneForwarded'
import SmsFailedOutlinedIcon from '@mui/icons-material/SmsFailedOutlined'
import ReportOutlinedIcon from '@mui/icons-material/ReportOutlined'
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined'
import YoutubeSearchedForIcon from '@mui/icons-material/YoutubeSearchedFor'
import { Alert, Snackbar } from '@mui/material'
import { useState } from 'react'

interface IMenuProps {
  anchorEl: HTMLElement | null
  open: boolean
  onClose: () => void
}

export const Menu: React.FC<IMenuProps> = ({ anchorEl, open, onClose }) => {
  const [message, setMessage] = useState('')

  const onClick = (text: string) => {
    setMessage(text)
  }

  const handleClose = (event: any, reason?: string) => {
    if (reason === 'clickaway') {
      return
    }
    setMessage('')
  }

  return (
    <>
      <MuiMenu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        anchorEl={anchorEl}
        open={open}
        onClose={onClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
      >
        <MenuList>
          <MenuItem onClick={() => onClick('  ')}>
            <ListItemIcon>
              <BorderColorOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText
              primary="  "
              secondary="  jam"
            />
          </MenuItem>
          <MenuItem onClick={() => onClick(' ')}>
            <ListItemIcon>
              <RadioButtonCheckedOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText> </ListItemText>
          </MenuItem>
          <Divider />

          <MenuItem onClick={() => onClick(' ')}>
            <ListItemIcon>
              <DashboardTwoToneIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText> </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick(' ')}>
            <ListItemIcon>
              <FullscreenTwoToneIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText> </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick('  ')}>
            <ListItemIcon>
              <AutoAwesomeIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText>  </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick(' ')}>
            <ListItemIcon>
              <ClosedCaptionOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText> </ListItemText>
          </MenuItem>
          <MenuItem
            onClick={() =>
              onClick('    ')
            }
          >
            <ListItemIcon>
              <PhoneForwardedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText>
                  
            </ListItemText>
          </MenuItem>
          <Divider />

          <MenuItem onClick={() => onClick('  ')}>
            <ListItemIcon>
              <SmsFailedOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText>  </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick('  ')}>
            <ListItemIcon>
              <ReportOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText>  </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick('   ')}>
            <ListItemIcon>
              <YoutubeSearchedForIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText>   </ListItemText>
          </MenuItem>
          <MenuItem onClick={() => onClick('')}>
            <ListItemIcon>
              <SettingsOutlinedIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText></ListItemText>
          </MenuItem>
        </MenuList>
      </MuiMenu>
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        color="warning"
        open={!!message}
        onClose={handleClose}
        message={message}
        autoHideDuration={4000}
      >
        <Alert onClose={handleClose} severity="info">
          {message}
        </Alert>
      </Snackbar>
    </>
  )
}
